//获取试卷并跳转到添加题库
<template>
  <div class="exam">
    <el-table :data="pagination.records" border>
      <el-table-column fixed="left" prop="source" label="试卷名称" width="180"></el-table-column>
      <el-table-column prop="description" label="介绍" width="200"></el-table-column>
      <el-table-column prop="institute" label="所属学院" width="120"></el-table-column>
      <el-table-column prop="major" label="所属专业" width="200"></el-table-column>
      <el-table-column prop="grade" label="年级" width="100"></el-table-column>
      <el-table-column prop="examDate" label="考试日期" width="120"></el-table-column>
      <el-table-column prop="totalTime" label="持续时间" width="120"></el-table-column>
      <el-table-column prop="totalScore" label="总分" width="120"></el-table-column>
      <el-table-column prop="type" label="试卷类型" width="120"></el-table-column>
      <el-table-column prop="tips" label="考生提示" width="400"></el-table-column>
      <el-table-column fixed="right" label="操作" width="230">
        <template slot-scope="scope">
          <el-button @click="importInfo(scope.row.paperId,scope.row.source)" type="success" size="small" icon="el-icon-upload el-icon--right">导入题库</el-button>
          <!-- <el-upload
          class="upload"
          action="#"
          :show-file-list="false"
          :on-change="handleExcel"
          accept="'.xlsx','.xls'"
          :auto-upload="false"
          :headers="headers" style="float: left; margin-right: 10px;">
              <el-button size="small" type="success" icon="el-icon-upload el-icon--right">导入题库</el-button>            
          </el-upload> -->
          <el-button @click="add(scope.row.paperId,scope.row.source)" type="primary" size="small">增加题库</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="导入题库"
      :visible.sync="dialogVisibleNew"
      width="40%"
      :before-close="handleClose" style="height:100%">
      <div style="margin-bottom: 10px;float: left;margin-left: 150px;padding-top: 10px;"><span style="color: red;">*</span>注：需要按照模板进行导入</div>
      <el-button type="primary" style="margin-left: 50px;margin-bottom: 30px;" @click="downloadDemo" :loading="loadingDemo" icon="el-icon-download" >下载模板</el-button>
          <el-upload
          ref="upload"
            class="upload-demo"
            drag
            :action="act"
            multiple 
            :headers="headers"
            :on-success="handleSuccess1"
            :before-upload="beforeUpload"
            style="text-align: center;">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          </el-upload>
       
    </el-dialog>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagination.current"
      :page-sizes="[4, 8, 10, 20]"
      :page-size="pagination.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total" class="page">
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisibleNew:false,
      headers: {"Content-Type": "multipart/form-data;charset=UTF-8"},
      form: {}, //保存点击以后当前试卷的信息
      pagination: { //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 4 //每页条数
      },
      act:"/${process.env.otherApi}/importExcel/",
      headers:{
        token:sessionStorage.getItem("token")
      },
      loadingDemo:false
    }
  },
  created() {
    this.getExamInfo()
  },
  methods: {
    downloadDemo(){
      
      setTimeout(() => {
        console.log('已经下载了2秒');
        
        const link = document.createElement("a"); // 创建a标签
        var url="https://exam-xr.oss-cn-hangzhou.aliyuncs.com/online_exam/2024/01/19/%E5%AF%BC%E5%85%A5%E9%A2%98%E5%BA%93%E6%A8%A1%E6%9D%BF.xlsx"
        link.href = url;
        link.download ="导入题模板"+".xlsx"; // 重命名文件
        link.click();
        URL.revokeObjectURL(url);
        this.$message({
          type:'success',
          message:'下载成功'
        })
        this.loadingDemo=false
      
      }, 1000);
      this.loadingDemo=true

    },
    beforeUpload(file){
      var str=String(file.name)
      if(str.substring(str.lastIndexOf(".")+1,str.length)=="xlsx"||str.substring(str.lastIndexOf(".")+1,str.length)=="xls")
        return true
      else{
        this.$message({
          message:"文件类型不匹配，建议检查文件",
          type:'error'
        })
        return false;
      }
        
    },
    handleSuccess1(response, file, fileList) {
      // 提交保存成功后清空已上传的文件
      if(response.code==400){
        this.$message({
          message:"导入题库成功",
          type:'success'
        })
        this.dialogVisibleNew=false
        this.handleSuccess()
      }
      if(response.code==200){
        this.$message({
          message:"导入题库失败，请检查文件",
          type:'error'
        })
        this.handleSuccess()
      }


    },
    handleSuccess(response, file, fileList) {
      // 提交保存成功后清空已上传的文件
      this.$refs.upload.clearFiles();
    },
    handleClose(done) { //关闭提醒
      this.$confirm('确认关闭？')
        .then(_ => {
          this.handleSuccess()
          done();
        }).catch(_ => {});
    },
    importInfo(paperId,source){
      this.dialogVisibleNew=true
      this.act="/${process.env.otherApi}/importExcel/"+paperId
    },
    handleExcel(file) {
      let formData = new FormData(); //声明一个FormDate对象
      formData.append("formFile", file.raw); 
    },
    getExamInfo() { //分页查询所有试卷信息
      this.$axios(`/${process.env.examApi}/exams/${this.pagination.current}/${this.pagination.size}`).then(res => {
        this.pagination = res.data.data
      }).catch(error => {
      })
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val
      this.getExamInfo()
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val
      this.getExamInfo()
    },
    add(paperId,source) { //增加题库
      this.$router.push({path:'/addAnswerChildren',query: {paperId: paperId,subject:source}})
    }
  },
};
</script>
<style lang="less" scoped>
.exam {
  padding: 0px 40px;
  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .edit{
    margin-left: 20px;
  }
}
</style>



